body {
  max-width: 767px;
  margin: auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-navbar-blue {
    background-image: -webkit-linear-gradient(top, #d77fe4 0%, #349de4 100%);
    background-image: linear-gradient(to bottom, #d77fe4 0%, #349de4 100%);
    background-color: transparent;
    border-color: transparent;
}

.app-container {
    height: 50px;
    color: white;
}

.app-container-title {
    margin-left: 40px;
    font-size: 25pt;
    font-weight: bold;
}

.app-content {
    margin-top: 50px;
    /*border: 1px solid black;*/
}

.menu-container {
    width: 100%;
    height: 100%;
    position: relative;
}

.menu-container > .menu-wrapper {
    width: 100%;
    height: 100%;
    margin: auto;
    overflow: hidden;
    position: relative;
    border-radius: 50%;
    background: transparent;
    pointer-events: auto;
}

.menu-container > .menu-wrapper:after {
    width: 50%;
    height: 50%;
    top: 25%;
    left: 25%;
    color: transparent;
    content: ".";
    display: block;
    position: absolute;
    border-radius: 50%;
    background-color: white;
}

.menu-container > .menu-wrapper li {
    width: 80%;
    height: 80%;
    top: -30%;
    left: -30%;
    position: absolute;
    overflow: hidden;
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    pointer-events: none;
    border: 5px solid white;
    background-image: -webkit-linear-gradient(top, #d77fe4 0%, #349de4 100%);
    background-image: linear-gradient(to bottom, #d77fe4 0%, #349de4 100%);
    opacity: 0.8;
}

.menu-container > .menu-wrapper li:first-child {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.menu-container > .menu-wrapper li:nth-child(2) {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}

.menu-container > .menu-wrapper li:nth-child(3) {
    -webkit-transform: rotate(225deg);
            transform: rotate(225deg);
}

.menu-container > .menu-wrapper li:nth-child(4) {
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
}

.menu-container > .menu-wrapper li > span {
    width: 100%;
    height: 40%;
    left: 20%;
    bottom: 10%;
    display: table;
    position: absolute;
    pointer-events: auto;
    text-align: center;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    color: #FFF;
    cursor: pointer;
}

.menu-container > .menu-wrapper li > span > span {
    display: table-cell;
    position: absolute;
    top: 15%;
    width: 100%;
}

.menu-container > ul {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    display: contents;
}

.menu-container > ul > li {
    width: 46%;
    height: 46%;
    top: 27%;
    left: 27%;
    position: absolute;
    list-style: none;
    border-radius: 50%;
    pointer-events: auto;
    display: table;
    background-image: -webkit-linear-gradient(top, #d77fe4 0%, #349de4 100%);
    background-image: linear-gradient(to bottom, #d77fe4 0%, #349de4 100%);
    opacity: 0.8;
}

.menu-container > ul > li > span {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    border-radius: 50%;
    color: #FFF;
    cursor: pointer;
}

@media (max-width: 479px) {
    .app-container-title {
        margin-left: 20px;
        font-size: 20pt;
    }
}
